import "../../App.css"
import { Link } from "react-router-dom";
import { PLAY_RESUME } from './AudioProvider'

export default function Navigation(props) {
    function onClick(audioPlayer) {
        console.log("stopping currently playing - on moving to other")
        const currentlyPlayingIndex = audioPlayer.findIndex(audio => audio[1] === PLAY_RESUME);
        const indexOfAudioToPlayOrResume = currentlyPlayingIndex === -1 ? 0 : currentlyPlayingIndex;
        audioPlayer[indexOfAudioToPlayOrResume][0].pause();
        if (props.setIsPlaying) {
            props.setIsPlaying(false);
        }
    }
    function getUrlText(item) {
        let urlDescription;
        if(item[0].includes("a")) {
            urlDescription = item[0].replace("a", "") +  ' ' + item[1];
        } else {
            urlDescription = item[0];
        }
        return urlDescription;
    }
    return (
        <span className="group">
            {"     "}
            {props.prevItem.length > 0 ? <Link onClick={() => onClick(props.audioPlayer)}
                to={"/" + props.prevItem[0]}>{getUrlText(props.prevItem)}</Link> : <span></span>}
            {"   "}
            {props.nextItem.length > 0 ? <Link onClick={() => onClick(props.audioPlayer)}
                to={"/" + props.nextItem[0]}>{getUrlText(props.nextItem)}</Link> : <span></span>}
            {"   "}
            <Link onClick={() => onClick(props.audioPlayer)}
                to={"/"}>அதிகாரங்கள்</Link>
            {/* <Link to={"/topic"}>அதிகாரம்</Link> */}
        </span>
    );
}

