import "../../App.css"
import Kural from './Kural'
import AudioPlayer from './AudioPlayer'
import { getAudioPlayer } from './AudioProvider'
import { Link } from "react-router-dom";
import Navigation from './Navigation'
import { getTitle } from './DataProvider';

export default function KuralAudio(props) {
    const mediaFiles = props.items.map(kural => '/kural/media/' + kural.id.toString() + '.mp3');
    const audioPlayer = getAudioPlayer(mediaFiles);

    const currentPathname = window.location.href; ///kural/a2
    console.log(currentPathname);
    const pathIdentifier = currentPathname.split("/");
    const itemIdentifier = pathIdentifier[pathIdentifier.length - 1];
    const isGroup = itemIdentifier.includes("a") ? true : false;
    const itemId = Number.parseInt(itemIdentifier.replace('a', ''));
    let nextItem = [];
    let prevItem = [];
    console.log(itemId)
    if (isGroup) {
        if (itemId + 1 <= 1079) {
            nextItem = getTitle("a" + (itemId + 1).toString());
        }
        if ((itemId - 1) > 0) {
            prevItem = getTitle("a" + (itemId - 1).toString());
        }
    } else {
        nextItem = [(itemId + 1).toString(), (itemId + 1).toString(), (itemId + 1).toString()];
        prevItem = [(itemId - 1).toString(), (itemId - 1).toString(), (itemId - 1).toString()];
    }
    // const nextItem = isGroup ? "a" + (itemId + 1) : (itemId + 1).toString()
    // const prevItem = isGroup ? "a" + (itemId - 1) : (itemId - 1).toString()
    return (
        <>
            <main className="flyin-grid">
                <>


                    <AudioPlayer mediaFiles={mediaFiles} audioPlayer={audioPlayer} prevItem={prevItem} nextItem={nextItem} />
                    <Kural items={props.items} />
                    <p>
                        <Navigation prevItem={prevItem} nextItem={nextItem} audioPlayer={audioPlayer} />
                    </p>
                </>
            </main>
        </>
    );
}
