import "../../App.css"
import { Link } from "react-router-dom";
import { getDistinctTopics } from './DataProvider'


export default function ThirukkuralListing() {
    return (<>
        <main className="flyin-grid">
            {getDistinctTopics().map((item, index) => {
                const topicId = "a" + (index + 1).toString();
                return (
                    <div key={topicId} className="flyin-grid__item card">
                        <p>

                            <Link to={"/" + topicId}>{index + 1}{'     '}{item}</Link>
                        </p>
                    </div>
                );
            }
            )}
        </main>
    </>

    );
}