import {
  // BrowserRouter as Router,
  HashRouter as Router,
  Routes,
  Route,
  Link
} from 'react-router-dom'

import '../App.css';

import SchoolSyllabus from './Thirukkural/SchoolSyllabus';
import Card from './Thirukkural/Card';
import Kural from './Thirukkural/Kural';
import KuralAudio from './Thirukkural/KuralAudio';
import data from '../data/kural.json';
import ThirukkuralListing from './Thirukkural/ThirukkuralListing'
import { getDistinctGroupIds } from './Thirukkural/DataProvider';

function App() {


  return (
    // <Router basename="/kural">
    <Router>

      <p className="copyright">
        Data Source: <a href='https://www.tamilvu.org/library/l2100/html/l2100ind.htm'>தமிழ் இணையக் கல்விக்கழகம்</a>
      </p>
      <Routes>

        {data.map(item => {
          const routePath = "/" + item.id.toString();
          const items = [item]
          return <Route key={item.id} path={routePath} element={<KuralAudio items={items} />} />
        }
        )}
        {
          getDistinctGroupIds().map(group => {
            const routePath = "/" + group;
            return <Route key={group} path={routePath}
              element={<KuralAudio items={data.filter(item => item.a_id === group)} />} />
          })
        }
        <Route path="/card" element={<Card kuralId="1" />} />
        <Route path="/" element={<ThirukkuralListing />} />
        {/* <Route path="/%E0%AE%A4%E0%AE%BF" element={<Card kuralId="1" />} />
        <Route path="/sg/5" element={<SchoolSyllabus kuralId="1" />} />
        <Route path="/tn/6" element={<SchoolSyllabus kuralId="2" />} />
        <Route path="/in/tn/6" element={<SchoolSyllabus kuralId="2" />} /> */}
      </Routes>
    </Router>
  );
}

export default App;
