import React, { useState } from "react"
import { PLAY_RESUME } from './AudioProvider'
import Navigation from './Navigation'

function AudioPlayer(props) {
    const [isPlaying, setIsPlaying] = useState(false);


    const audioPlayer = props.audioPlayer;
    // const canActOnPlayer = isPlaying ? 'Pause' : 'Play';
    const canActOnPlayer = 'Play! Pause...';
    return (
        <div className="flyin-grid__item">
            {/* <p>files to play: {props.mediaFiles}</p> */}
            <p>
                <button className="btn-play" onClick={(e) => {

                    const currentlyPlayingIndex = audioPlayer.findIndex(audio => audio[1] === PLAY_RESUME);
                    const indexOfAudioToPlayOrResume = currentlyPlayingIndex === -1 ? 0 : currentlyPlayingIndex;

                    console.log('indexOfAudioToPlay:' + indexOfAudioToPlayOrResume);

                    if (isPlaying) {
                        audioPlayer[indexOfAudioToPlayOrResume][0].pause();
                        setIsPlaying(false);
                    } else {
                        audioPlayer[indexOfAudioToPlayOrResume][0].play();
                        setIsPlaying(true);
                    }
                }}>{canActOnPlayer}</button>
                <Navigation prevItem={props.prevItem} nextItem={props.nextItem} audioPlayer={props.audioPlayer} setIsPlaying={setIsPlaying}/>
            </p>

        </div>)

}

export default AudioPlayer;