import data from '../../data/kural.json';

function getDistinctTopics() {

  return Object.keys(data.reduce(function (r, a) {
    r[a["labels"][0]] = r[a["labels"][0]] || [];
    r[a["labels"][0]].push(a);
    return r;
  }, Object.create(null)));
};

function getDistinctGroupIds() {
  return Object.keys(data.reduce(function (r, a) {
    r[a.a_id] = r[a.a_id] || [];
    r[a.a_id].push(a);
    return r;
  }, Object.create(null)))
}

function getTitle(groupId) {
  const selectedItems = data
    .filter(item => item.a_id === groupId);

  console.log(selectedItems);
  return selectedItems.length > 0 ? [selectedItems[0].a_id,
  selectedItems[0].labels[0],
  selectedItems[0].labels[1]] : []

}

